ul {
    list-style: none;
}

li {
  margin-bottom: 1rem;
}

.coverpage {
    margin: 0 2rem;
    align-content: center;
}

#cross-logo {
    display: block;
    max-width: 240px;
    height: auto;
    margin: auto;
}

.booktitle {
  text-align: center;
  margin-top: 70px
}

.booktitle a {
    color: inherit;
    text-decoration: none;
}

.booktitle b {
    font-size: x-large;
}

.booktitle small {
    font-size: x-small;
}

.randomverse {
    width: 260px;
    margin: auto;
}

.randomverse p {
    text-align: center;
    color: inherit;
    text-decoration: none;
}

.randomverse small {
    padding: 0 1rem 0 1rem;
}

.navbar {
    overflow: hidden;
    min-height: 60px;
    display: flex;
    justify-content: space-around;
    align-items: stretch;
    background: rgba(248, 249, 250, 0.7);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
}

.navbar a {
    color: inherit;
    text-decoration: none;
    display: flex;
}

.nav-item {
    background: transparent;
    box-sizing: border-box;
    position: relative;
    color: inherit;
    display: flex;
    margin: 0.5rem 1rem 0.5rem 0rem;
    padding: 0 0 0 0rem;
    justify-content: center;
    align-content: center;
    text-align: center;
    /* vertical-align: middle; */
}

.nav-item select {
    max-width: 50%;
    color: #363535;
    background: inherit;
    text-align: center;
    padding: 1rem 0.5rem 1rem 0.5rem;
    display: inline-block;
    height: fit-content;
    border-radius: 0.25em;
    font-size: var(--body-text);
}

.nav-item:first-child {
    margin: 0.5rem 2rem 0.5rem 45px;
}

.nav-item input {
    max-width: 50%;
    color: #363535;
    background: inherit;
    text-align: center;
    padding: 1rem 0rem 1rem 0rem;
    height: fit-content;
    border-style: solid;
    border-radius: 0.25em;
    font-size: var(--body-text);
}

.nav-item button {
    background: inherit;
    border-style: solid;
    border-radius: 2em;
    padding: 0.1rem 0.5rem 0.1rem 0.5rem;
    height: fit-content;
    margin-left: 0.5rem;
}

.vertical-center {
    margin: 0;
    position: absolute;
    top: 40%;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
}

.footerbar {
    overflow: hidden;
    min-height: 60px;
    display: flex;
    justify-content: space-around;
    align-items: stretch;
    background: rgba(248, 249, 250, 0.7);
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
}

.footer-item {
    background: transparent;
    box-sizing: border-box;
    color: inherit;
    display: inline-block;
    margin: 0.5rem 1rem 0.5rem 0;
    text-align: center;
    vertical-align: middle;
}

.footerbar svg:hover {
    background: #ddd;
    color: black;
}

.settings {
    width: 70%;
    margin: 30% auto;
    font-size: var(--body-text);
}

.settings-item {
    display: flex;
    justify-content: space-between;
}

.settings-item label {
    display: inline-block;
    margin: 20px 0px;
}

.settings-item select {
    display: inline-block;
    height: fit-content;
    margin: 15px;
    border-radius: 0.25em;
}

.toc {
    display: flex;
    width: 90%;
    margin: 10rem auto
}

.toc-card a {
    text-decoration: none;
    color: inherit
}

 .toc-card-header {
     font-weight: bold;
     text-align: center ;
 }

 .verses-list {
    width: 90%;
 }

 .verse-detail {
      margin-top: 0.5rem;
 }

 .verse-detail p {
     text-align: justify;
     margin-bottom: 0;
 }

 .verse-detail small {
     margin-left: 2rem;
 }

 .verse-detail em {
     margin-left: 0.5rem;
 }

 .verse-detail:hover {
    color: #efefef;
    background: #626262;
    text-decoration: none;
 }

 .verse-detail a {
    color: inherit;
    text-decoration: none;
 }

 .chapter {
     justify-content: center;
     text-align: justify;
     margin: 7rem 5%;
 }

 .chapter sup {
     color: darkred;
 }

 .close-page {
     position: absolute;
     top: 5px;
     right: 5px;
 }

 .about-page {
     margin: 7rem 10rem;
 }

 .about-text {
     margin-top: 2rem;
 }

 .about-footer {
     font-style: italic;
     font-size: smaller;
     position: fixed;
     bottom: 2rem;
 }
