html {
  font-size: 62.5%;
    --body-text: 1.6rem
}

@font-face {
    font-family: "Roboto-Regular";
    src: local("Roboto-Regular"),
        url("./fonts/Roboto/Roboto-Regular.ttf") format("truetype");
    font-weight: normal;
    font-display: swap;
}

body {
  font-family: "Roboto-Regular", sans-serif;
  font-size: var(--body-text);
}

.fontZoomM {
    font-size: var(--body-text);
}

.fontZoomL {
    font-size: large;
}

.fontZoomXL {
    font-size: x-large;
}

.fontZoomXXL {
    font-size: xx-large;
}

.fontZoomXXXL {
    font-size: xxx-large;
}

.nightTheme {
    background: #2C2F37;
    color: #8F95A1;
}

.dayTheme {
    background: #f4f2ff;
    color: #0f0f0f;
}

.sepiaTheme {
    background: #ffdfaf;
    color: #000000;
}